import React, {useState} from 'react'
import axios from "axios";
import "./LoginRegister.css"


export default function Signup() {
    const [ info, setInfo ] = useState({id: "", pw: "" , name: ""}) //      
    
    const onChange = (event) => {  //         ()
        const name = event.target.name //     
        const value = event.target.value

        setInfo(values => ({...values, [name] : value}))
    }

    const onSubmit = () => {
        console.log(`id : ${info.id}`)
        console.log(`pw : ${info.pw}`)
        console.log(`name : ${info.name}`)

        axios.post("http://localhost:3001/signup", {
                id : info.id,
                pw : info.pw,
                name : info.name,
        })
        .then(res => {
            let msg = res.data.msg
            console.log(msg);
            if (msg == "user already exists!") {
                alert(" ID.");
            }
            else {
                alert(" !")
                document.location.href = '/userlogin';    
            }
        });
    }


    return (
        <div className="loginregister">
            <form>
                <input name="name" type="text" placeholder="" value={ info.name || "" }  className="loginregister__input" onChange={ onChange } /><br/>
                <input name="id" type="id" placeholder="" value={ info.id || "" }  className="loginregister__input" onChange={ onChange } /><br/>
                <input name="pw" type="password" placeholder="" value={ info.pw || "" }  className="loginregister__input" onChange={ onChange } /><br/>
                {/* <input name="confirmPassword" type="password" placeholder=" " className="loginregister__input"/><br/> */}
                <button type="button" onClick={onSubmit} className="loginregister__button"> </button>
            </form>
        </div>
        
    )
}

