import { useEffect, useState } from "react"
import React from "react"
import axios from "axios"   //server  
import "./LoginRegister.css"

export default function Login() {
    const [ info, setInfo ] = useState({id: "", pw: ""})
    
    const onChange = (event) => {
        const name = event.target.name
        const value = event.target.value

        setInfo(values => ({...values, [name] : value}))
    }

    const onSubmit = () => {
        
        console.log(`id : ${info.id}`)
        console.log(`pw : ${info.pw}`)
        axios.get("http://localhost:3001/login", {
            params: {
                id: info.id,
                pw: info.pw
            }
        })
        .then(res => {
            console.log(res)
            // console.log('res.data.userId :: ', res.data.id)/
            console.log('res.data.msg :: ', res.data.msg);
            let msg = res.data.msg;
            if(msg === "no data found"){
                // id    userId = undefined, msg = ' id   .'
                console.log('======================',res.data.msg)
                alert('   .')
            } 
            // else if(res.data.userId === null){
            //     // id , pw    userId = null , msg = undefined
            //     console.log('======================','    .')
            //     alert('    .')
            // } 
            else {
                // id, pw   userId = userId1, msg = undefined
                console.log('======================',' ')
                sessionStorage.setItem('user_id', info.id)
                // //     ()
                alert(" !");
                document.location.href = '/';
            }
        })


    }


    return (
        <div className="loginregister">
            <form>
                <input name="id" type="id" placeholder="" value={ info.id || "" } className="loginregister__input" onChange={ onChange }/><br/>
                <input name="pw" type="password" placeholder="" value={ info.pw || "" } className="loginregister__input"onChange={ onChange } /><br/>
                <button type="button" onClick={onSubmit} className="loginregister__button"></button>
            </form>
        </div>
    )
}